import React, { useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';
import { TodoList } from './components/TodoList';
import { Filters } from './components/Filters';
import { useLocalStorage } from './custom_hooks/useLocalStorage';

function App() {
  const [title, setTitle] = useState('');
  const [todos, setTodos] = useLocalStorage('todos', []);
  const [filteredTodos, setfilteredTodos] = useState(todos);
  const [filterStatus, setFilterStatus] = useState('all');
  const [activeSelectAll, setActiveSelectAll] = useState(false);
  const [allCompleted, setAllCompleted] = useState(false);

  const addTitle = (value) => {
    setTitle(value);
  };

  const onSubmit = (event) => {
    event.preventDefault();

    if (title.length === 0) {
      return;
    }

    const newTodo = {
      title,
      id: uuidv4(),
      completed: false,
    };

    setTodos([...todos, newTodo]);
    setfilteredTodos([...todos, newTodo]);

    setTitle('');
  };

  const toggleAll = (status) => {
    const uncheckedTodos = todos.map(todo => (
      {
        ...todo,
        completed: status,
      }
    ));

    setAllCompleted(status);
    setTodos(uncheckedTodos);
    setfilteredTodos(uncheckedTodos);
  };

  useEffect(() => {
    if (todos.every(todo => todo.completed === true)) {
      setAllCompleted(true);
    }

    if (todos.every(todo => todo.completed === false)) {
      setAllCompleted(false);
    }
  }, [todos]);

  const changeStatus = (checkedTodoId) => {
    const todosCopy = todos.map((todo) => {
      if (todo.id === checkedTodoId) {
        return {
          ...todo,
          completed: !todo.completed,
        };
      }

      return todo;
    });

    setTodos(todosCopy);
    setfilteredTodos(todosCopy);
  };

  useEffect(() => {
    if (filterStatus === 'all') {
      setfilteredTodos(todos);
    } else {
      const filter = filterStatus === 'completed';

      const filteredList = todos.filter(todo => (
        todo.completed === filter
      ));

      setfilteredTodos(filteredList);
    }
  }, [filterStatus, todos]);

  const deleteTodo = (todoIdForDelete) => {
    const filteredList = todos.filter(todo => (
      todo.id !== todoIdForDelete
    ));

    setTodos(filteredList);
    setfilteredTodos(filteredList);
  };

  useEffect(() => {
    if (!activeSelectAll) {
      const checkStatus = todos.some(todo => (
        todo.completed === true
      ));

      if (checkStatus) {
        setActiveSelectAll(true);
      }
    } else {
      const checkStatus = todos.every(todo => (
        todo.completed === false
      ));

      if (checkStatus) {
        setActiveSelectAll(false);
      }
    }
  }, [todos, activeSelectAll]);

  const clearAllCompleted = () => {
    const filteredList = todos.filter(todo => (
      todo.completed === false
    ));

    setTodos(filteredList);
    setfilteredTodos(filteredList);

    if (filterStatus === 'completed') {
      setFilterStatus('all');
    }
  };

  const updateTodoItem = (todoId, newTitle) => {
    const todosCopy = todos.map((todo) => {
      if (todo.id === todoId) {
        return {
          ...todo,
          title: newTitle,
        };
      }

      return todo;
    });

    setTodos(todosCopy);
    setfilteredTodos(todosCopy);
  };

  return (
    <section className="todoapp">
      <header className="header">
        <h1>todos</h1>

        <form onSubmit={onSubmit}>
          <input
            type="text"
            className="new-todo"
            placeholder="What needs to be done?"
            value={title}
            onChange={event => addTitle(event.target.value)}
          />
        </form>
      </header>
      {!!todos.length && (
        <>
          <section className="main">
            <input
              type="checkbox"
              id="toggle-all"
              className="toggle-all"
              checked={allCompleted}
              onChange={event => toggleAll(event.target.checked)}
            />
            <label
              htmlFor="toggle-all"
              title="Mark all as completed/not completed"
            >
              Mark all as completed/not completed
            </label>

            <TodoList
              todos={filteredTodos}
              changeStatus={changeStatus}
              deleteTodo={deleteTodo}
              toggleAll={toggleAll}
              updateTodoItem={updateTodoItem}
            />
          </section>

          <footer className="footer">
            {filteredTodos.length !== 0 && (
              <span className="todo-count">
                {`${filteredTodos.length} item(s) left`}
              </span>
            )}

            <Filters
              setFilterStatus={setFilterStatus}
              filterStatus={filterStatus}
              activeSelectAll={activeSelectAll}
            />

            {activeSelectAll && (
              <button
                type="button"
                className="clear-completed"
                onClick={clearAllCompleted}
              >
                Clear completed tasks
              </button>
            )}

          </footer>
        </>
      )}
    </section>
  );
}

export default App;
